import axios from 'axios'
import { getUTCTimestamp } from './time'
import { AppStorage } from './utils'
import { config } from './../config/config'


const REST = config.restURL


export const ajax = (path, method = 'GET', body, options = { updateLastActive: true }) => {

  const { updateLastActive } = options

  const lastActive = AppStorage.lastActive || 0

  if (updateLastActive) {
    AppStorage.lastActive = getUTCTimestamp()
  }


  return axios({
    url: `${REST}${path}`,
    method,
    data: body,
    headers: {
      'last-active': lastActive
    },
  }).then(json => json.data);

}
